* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    text-decoration: none;
    list-style: none;
}

$main-grey: #1d1d1b;
$start-gradient: #f49718;
$end-gradient:#e20512;
$gradient-direction: 0.75turn;

.main-page {
    width: 100vw;
    height: 100vh;
    background: linear-gradient($gradient-direction, $start-gradient, $end-gradient);
    display: flex;
    align-items: center;
    justify-content: center;
    &__container {
        display: flex;
        align-items: center;
        justify-content: center;
        width:100%;
        height: 97%;
        background-color: $main-grey;
        vertical-align: middle;
    }
}


